import { average, prominent } from "color.js";
import Swal from "sweetalert2";
import ClipboardJS from "clipboard";
import Toastify from "toastify-js";

const colorBox = new ClipboardJS(".color-box");
colorBox.on("success", copyClipboard);

function copyClipboard() {
  Toastify({
    text: "Copied Successful!",
    backgroundColor:
      "linear-gradient(225deg, #FF3CAC 0%, #784BA0 50%, #2B86C5 100%)",
    duration: 3000,
  }).showToast();
}

function modal(event) {
  event.target.addEventListener("click", (el) => {
    const colors = el.target.dataset.colorSet.split(",");
    Swal.fire({
      html: `<div class="colors" >
            ${colors
              .map(
                (color) =>
                  `<span class="color-box" style="background-color: ${color};" data-clipboard-text="${color}"></span>`,
              )
              .join("")}
        </div>`,
      imageUrl: event.target.dataset.base64,
      imageWidth: 450,
      imageAlt: "A tall image",
      showConfirmButton: false,
    });
  });
}

document
  .querySelector(".image-container")
  .addEventListener("click", function (event) {
    if (event.target.classList.contains("preview-img")) {
      modal(event);
    }
  });

const myInput = document.querySelector("#myInput");
const imgContainer = document.querySelector(".image-container");

myInput.addEventListener("change", function () {
  [...this.files].map((file) => {
    const reader = new FileReader();
    reader.addEventListener("load", function () {
      if (!!document.querySelector(".title")) {
        document.querySelector(".title").remove();
      }
      const span = document.createElement("span");
      span.classList.add("preview-img");
      span.style.backgroundImage = "url(" + this.result + ")";
      span.dataset.base64 = this.result;
      imgContainer.appendChild(span);

      prominent(this.result, { amount: 5, format: "hex" }).then((colors) => {
        span.dataset.colorSet = colors.join(",");
      });
    });
    reader.readAsDataURL(file);
  });
});
